import Vue from "vue";
import Vuex from "vuex";
import axios from 'axios';
// import createPersistedState from 'vuex-persistedstate';
import VueRouter from '../router';

Vue.use(Vuex);

export default new Vuex.Store({
  // plugins: [createPersistedState()],
  state: {
    user: { email: "",
            admin:false },
    isAuth: false,
    serverUrl: 'http://localhost:8080',
    login_message: '',
    register_message:''
  },  
  getters: {
    user(state) {
      return state.user;
    },
    isAuth(state) {
      return state.isAuth;
    }
  },
  mutations: {
    clearAuthData(state) {
      state.isAuth = '';
      state.user = {};
    },
    authUser(state, UserData) {      
      state.isAuth = true;
      state.email = UserData.email;
      // if (UserData.admin === '1') {
      //   state.user.admin = true;
      // } else {
      //   state.user.admin = false;
      // }
      state.user.admin= (UserData.admin=== 'true');
    }
  },
  actions: {
    register({}, authData) {
      axios
        .post(`${this.state.serverUrl}/register`, {          
          email: authData.email,
          password: authData.password,
          confirmPassword: authData.confirmPassword,
          admin:authData.admin
        })
        .then(response => {
          this.state.register_message = response.data.Response;
          setTimeout(() => {
            this.state.register_message = '';
          }, 3000);         
        })
        .catch(error => {
          console.log(error);
        });
    },
    login({ commit }, authData) {
      axios
        .post(`${this.state.serverUrl}/login`, {
          email: authData.email,
          password: authData.password
        })
        .then(res => {
          this.state.login_message = res.data.Response;
          if (res.data.Response === 'Login Successful') {
            commit('authUser', {
              email: authData.email,
              admin: res.data.Admin
            });            
            VueRouter.push({ name: 'Dashboard' });            
          };
          setTimeout(() => {
            this.state.login_message = '';
          }, 2000);          
        })
        .catch(err => {
          console.log(`error! + ${err}`);
        });
    },
    logout({ commit }) {
      commit("clearAuthData");
      VueRouter.replace("/");
    }
  }
});
